<template>
  <div>
    <el-row>
      <el-col :span="4" v-for="(item, index) in imgList" :key="index" :offset="index > 0 ? 1 : 0">
        <el-card :body-style="{ padding: '0px' }">
          <!-- <div class="img-item"> -->
          <img :src="$axios.defaults.baseURL+item" class="image" />
          <div style="padding: 14px;">
            <span>该图片没有描述</span>
            <div class="bottom clearfix">
              <el-button class="button" size="mini" type="primary"
              v-clipboard:copy='$axios.defaults.baseURL+item'
              v-clipboard:success='onCopy'>复制链接</el-button>
              <el-button class="button" size="mini" icon='el-icon-delete' type="warning" @click="delImg(item)">删除</el-button>
            </div>
          </div>
          <!-- </div> -->
        </el-card>

      </el-col>
      <el-col :span='8' class="upload" :offset="1">
        <el-card>
          <el-upload
            drag
            :action="$axios.defaults.baseURL+'api/upload'"
            name='pic'
            :on-success='handleUpload'
            :show-file-list='false'
          >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">
              将文件拖到此处，或
              <em>点击上传</em>
            </div>
          </el-upload>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<style>
.upload{
  width: 400px;
}
.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}
/* .img-item{
    width: 200px;
  } */
.image {
  width: 100%;
  display: block;
  object-fit: cover;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
</style>

<script>
export default {
  data () {
    return {
      currentDate: new Date(),
      imgList: []
    }
  },
  mounted () {
    this._getImgList()
  },
  methods: {
    _getImgList () {
      this.$axios.get('api/list').then(res => {
        if (res.data.meta.status !== 200) { return this.$message.error('获取图片失败') }
        const list = res.data.data.map(v => v.slice(7))
        this.imgList = list
      })
    },
    handleUpload (res, file) {
      if (res === 'done') {
        this.$message.success('上传成功')
        this._getImgList()
      } else {
        this.$message.error('上传失败')
      }
    },
    // 复制图片链接
    onCopy (url) {
      // console.log(url)
      this.$message.success('复制成功')
    },
    async delImg (url) {
      // 删除图片
      console.log(url)
      const delImg = 'upload/' + url
      console.log(delImg)
      const { data: res } = await this.$axios.delete('api/del', { params: { delImg } })
      if (res.meta.status !== 204) {
        return this.$message.error('删除失败')
      }
      this._getImgList()
      this.$message.success('删除成功')
    }
  }
}
</script>
